<template>
  <div class="NewBlog">
    <!--        <section class="blog-body">-->
    <div class="admin-publish">
      <el-input
        v-model="title"
        placeholder="文章标题"
        class="title"
        autofocus
      ></el-input>
      <el-input
        v-model="summary"
        placeholder="文章概要"
        class="summary"
      ></el-input>
      <!--                <div id="editor" style="text-align:left"></div>-->
      <el-row class="row">
        <el-col :span="16">
          <el-select
            v-model="tags"
            multiple
            :popper-append-to-body="false"
            placeholder="请选择标签"
            style="width: 100%"
          >
            <el-option
              v-for="item in options"
              :key="item.tag_id"
              :label="item.tag_name"
              :value="item.tag_name"
            ></el-option>
          </el-select>
        </el-col>
        <el-col :span="6" :offset="2">
          <el-button type="primary" size="medium" @click="saveDraft"
            >存为草稿</el-button
          >
          <el-button type="primary" size="medium" @click="publish"
            >发布</el-button
          >
        </el-col>
      </el-row>
    </div>

    <el-upload
      class="avatar-uploader"
      :action="serverUrl"
      name="img"
      :headers="header"
      :show-file-list="false"
      :on-success="uploadSuccess"
      :on-error="uploadError"
      :before-upload="beforeUpload"
    >
    </el-upload>
    <!--富文本编辑器组件-->
    <el-row v-loading="quillUpdateImg">
      <quill-editor
        class="editor2"
        v-model="content"
        ref="myQuillEditor"
        :options="editorOption"
        @change="onEditorChange($event)"
        @ready="onEditorReady($event)"
      >
      </quill-editor>
    </el-row>
  </div>
</template>
<script>
import { addQuillTitle } from '@/api/quill-title.js'
import { quillEditor } from 'vue-quill-editor'
import 'quill/dist/quill.snow.css'
import 'quill/dist/quill.bubble.css'
import 'quill/dist/quill.core.css'
import toolbarOptions from 'api/editorConfig'
// import sensitiveWord from '@/api/SENSITIVEWORDS'
import { a, b, c, d, e, f } from 'api/sensitive_words'
// import sensitiveWord from "../../../api/SENSITIVEWORDS";
// const wc = require ('sensitive-word-filter').default();
// import {wc} from 'sensitive-word-filter'
// var wc = require('word-sensitive')
// import {wc} from 'word-sensitive'
// const SensitiveWords = require("js-sensitivewords");
//
// const sw = new SensitiveWords();
//
// // 初始化敏感词库，建议在启动的时候执行
// sw.addWords(["天安门", "毛主席", "周总理"]);
// sw.addWord("管理员");
//
// // 判断是否包含敏感词，返回值 true: 包含; false: 不包含
// sw.containsDfa("我爱北京天安门"); // => true
// sw.containsDfa("主席毛泽东同志"); // => false
//
// // 获取含有的敏感词内容，返回值 字符串数组
// sw.wordsDfa("我爱毛主席和周总理"); // => ['毛主席', '周总理']
//
// // 替换敏感词，当once=true时，对于每一个出现的敏感词，只替换一次。默认 once = false
// sw.replaceDfa("我是管理员", "*", true); // => 我是*
// // 替换敏感词, 当once=false时，对于每一个出现的敏感词，按照其长度替换。默认 once = false
// sw.replaceDfa("我是管理员", "*", false); // => 我是***

// var now = new Date();
Date.prototype.toLocaleString = function () {
  // 重写日期函数格式化日期
  return `${this.getFullYear()}-${
    this.getMonth() + 1 >= 10
      ? this.getMonth() + 1
      : '0' + (this.getMonth() + 1)
  }-${this.getDate() >= 10 ? this.getDate() : '0' + this.getDate()}
                 ${
                   this.getHours() >= 10
                     ? this.getHours()
                     : '0' + this.getHours()
                 } : ${
    this.getMinutes() >= 10 ? this.getMinutes() : '0' + this.getMinutes()
  } : ${this.getSeconds() >= 10 ? this.getSeconds() : '0' + this.getSeconds()}`
}

// import editor from './Editor.vue'
export default {
  name: 'NewBlog',
  data() {
    return {
      // isSensitive: false,
      sensitive: [
        a.split('\n'),
        b.split('\n'),
        c.split('\n'),
        d.split('\n'),
        e.split('\n'),
        f.split('\n'),
      ],
      sensitiveIndex: true,
      postUrl: '/api/blog/saveBlog',
      blog: {},
      // uploadImgUrl: "/api/blog/img",
      html: '',
      time: '',
      options: [],
      tags: [],
      title: '',
      summary: '',
      content: '',

      quillUpdateImg: false, // 根据图片上传状态来确定是否显示loading动画，刚开始是false,不显示
      serverUrl: '/api/upload/image', // 这里写你要上传的图片服务器地址
      header: { token: sessionStorage.token }, // 有的图片服务器要求请求头需要有token之类的参数，写在这里
      // detailContent: '', // 富文本内容
      editorOption: {
        placeholder: '',
        theme: 'snow', // or 'bubble'
        modules: {
          toolbar: {
            container: toolbarOptions, // 工具栏
            handlers: {
              image: function (value) {
                if (value) {
                  // 触发input框选择图片文件
                  document.querySelector('.avatar-uploader input').click()
                } else {
                  this.quill.format('image', false)
                }
              },
            },
          },
        },
      }, // 富文本编辑器配置
    }
  },
  created() {
    // console.log(this.$refs.myQuillEditor.quill);

    if (this.$route.path === '/editBlog') {
      this.postUrl = '/api/blog/updateBlog'
      this.$axios
        .post('/api/blog/getBlogById', {
          article_id: this.$route.query.article_id,
        })
        .then((response) => {
          let res = response.data
          if (res.status) {
            this.$message.error(res.msg)
            this.$router.go(-1)
          } else {
            console.log(res)
            console.log(res.msg)
            console.log(res.result)
            this.blog = res.result[0]
            // this.tags = this.blog.tag
            this.content = this.blog.content
            this.summary = this.blog.summary
            this.title = this.blog.title
          }
        })
    }

    this.$axios.get('/api/tag/getAllTag').then((response) => {
      let res = response.data
      if (res.status) {
        this.$message({
          message: res.msg,
          type: 'warning',
        })
      } else {
        // let str = res.result
        //     .substring(1, this.blog.tag.length - 1)
        //     .replace(/\"/g, "");
        // this.tags = str.split(",");
        this.options = [...res.result]
      }
    })

    this.time = new Date()
    // console.log(this.time);
    this.time = new Date(this.time).toLocaleString().split('\n')
    // console.log(this.time);
    // console.log(time[0]);
  },
  watch: {
    // isSensitive(){
    //     console.log(this.isSensitive);
    // }
  },
  mounted() {
    addQuillTitle()
  },
  methods: {
    onEditorChange($event) {},
    onEditorReady($event) {},
    // 富文本图片上传前
    beforeUpload() {
      // 显示loading动画
      this.quillUpdateImg = true
    },

    uploadSuccess(res, file) {
      console.log(res)
      // res为图片服务器返回的数据
      // 获取富文本组件实例
      let quill = this.$refs.myQuillEditor.quill
      // 如果上传成功
      if (res.status === 0 && res.img !== null) {
        // 获取光标所在位置
        let length = quill.getSelection().index
        // 插入图片  res.info为服务器返回的图片地址
        quill.insertEmbed(length, 'image', res.img)
        // 调整光标到最后
        quill.setSelection(length + 1)
      } else {
        this.$message.error('图片插入失败')
      }
      // loading动画消失
      this.quillUpdateImg = false
    },

    // 富文本图片上传失败
    uploadError() {
      // loading动画消失
      this.quillUpdateImg = false
      this.$message.error('图片上传失败')
    },

    publish() {
      if (!this.title.length) {
        this.$message({
          message: '标题不能为空',
          type: 'warning',
        })
      } else if (!this.summary.length) {
        this.$message({
          message: '概要不能为空',
          type: 'warning',
        })
      } else if (!this.tags.length) {
        this.$message({
          message: '请选择标签',
          type: 'warning',
        })
      } else if (!this.content.length) {
        this.$message({
          message: '内容不能为空',
          type: 'warning',
        })
      } else {
        // let tempContent = this.content.substr(3,this.content.length-7)
        this.sensitive.forEach((item) => {
          // let itemArray = item.split('\n')
          item.forEach((itemchild) => {
            if (this.content.includes(itemchild)) {
              this.isSensitive = true
              console.log(item)
              console.log(itemchild)
            }
          })
        })
        // let summary1 = wc.filter(this.summary);
        // let title1 = wc.filter(this.title);
        // let content1 = wc.filter(this.content);
        // wc.

        if (this.isSensitive) {
          // if (content1 !== this.content || summary1 !== this.summary || title1 !== this.title) {
          //     this.title = title1;
          //     this.summary = summary1;
          //     this.content = content1;
          this.$message.warning('含有敏感词,请重新修改!')
          this.isSensitive = false
        } else {
          this.$axios
            .post(this.postUrl, {
              user_id: sessionStorage.getItem('user_id'),
              article_id: this.blog.article_id,
              title: this.title.trim(),
              summary: this.summary.trim(),
              article_html:
                document.querySelector('.editor2').children[0].innerHTML,
              content: this.content,
              // tag: this.tags,

              tag: JSON.stringify(this.tags),
              state: 1,
              post_time: this.time[0],
            })
            .then((response) => {
              if (response.data.status == 0) {
                this.$message({
                  message: '发布成功',
                  type: 'success',
                })
                this.title = ''
                this.content = ''
                this.tags = []
                this.summary = ''
                this.$router.push('/admin/blog')
              } else if (response.data.status == 1) {
                this.$message.error(response.data.msg)
              }
            })
            .catch((error) => {
              console.log(error)
            })
        }
      }
    },
    saveDraft() {
      if (!this.title.length) {
        this.$message({
          message: '标题不能为空',
          type: 'warning',
        })
      } else if (!this.summary.length) {
        this.$message({
          message: '概要不能为空',
          type: 'warning',
        })
      } else if (!this.tags.length) {
        this.$message({
          message: '请选择标签',
          type: 'warning',
        })
      } else if (!this.content.length) {
        this.$message({
          message: '内容不能为空',
          type: 'warning',
        })
      } else {
        this.$axios
          .post(this.postUrl, {
            user_id: sessionStorage.getItem('user_id'),
            article_id: this.blog.article_id,
            title: this.title.trim(),
            summary: this.summary.trim(),
            article_html:
              document.querySelector('.editor2').children[0].innerHTML,
            content: this.content,
            // tag: this.tags,
            tag: JSON.stringify(this.tags),
            post_time: this.time[0],
            state: 0,
          })
          .then((response) => {
            if (response.data.status == 0) {
              this.$message({
                message: '保存成功',
                type: 'success',
              })

              // console.log(response);
              this.title = ''
              this.content = ''
              this.tags = []
              this.summary = ''
              this.$router.push('/admin/draft')
            } else if (response.data.status == 1) {
              this.$message.error(response.data.msg)
            }
          })
          .catch((error) => {
            console.log(error)
          })
      }
    },

    // publish(){
    //
    // }
  },
}
</script>

